
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonSearchbar,
  IonButtons,
  IonMenu,
  IonList,
  IonItem,
  IonMenuButton,
  menuController,
  onIonViewDidLeave,
  IonButton,
  IonIcon,
  loadingController,
  IonLabel,
  IonGrid,
  IonRow,
  IonCol,
  IonText,
  IonNote,
  IonFooter,
  alertController,
  IonListHeader,
  IonSegment,
  IonSegmentButton,
  IonThumbnail,
  IonChip,
} from "@ionic/vue";
import { computed, defineComponent, onMounted, ref, watch } from "vue";
import { searchSharp, close } from "ionicons/icons";
import NeopleApi from "@/utils/NeopleApi";
import Common from "@/utils/Common";
import NoData from "@/components/NoData.vue";
import { IDBPDatabase, openDB } from 'idb';

export default defineComponent({
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonSearchbar,
    IonButtons,
    IonMenu,
    IonList,
    IonItem,
    IonMenuButton,
    IonButton,
    IonIcon,
    IonLabel,
    IonGrid,
    IonRow,
    IonCol,
    IonText,
    IonNote,
    IonFooter,
    IonListHeader,
    IonSegment,
    IonSegmentButton,
    IonThumbnail,
    NoData,
    IonChip
  },
  setup() {
    const playerName = ref("");
    const playerId = ref("");
    const playerInfo = ref();
    const gameTypeId = ref('');
    const next = ref('');
    const matches = ref();
    const searchWords = ref();
    const tableName = 'search-words';
    let idb: IDBPDatabase<unknown>;

    const playerSearch = async () => {
      const loading = await loadingController.create({
        message: " ",
        mode: "ios",
      });
      loading.present();

      try {
        const playerIdJson = await NeopleApi.cyPlayerId({
          nickname: encodeURI(playerName.value),
        });

        if (Common.isNull(playerIdJson.rows)) {
          playerId.value = "";
          playerInfo.value = null;
        } else {
          playerId.value = playerIdJson.rows[0].playerId;
          playerInfo.value = await NeopleApi.cyPlayerMatches(
            Common.preprocessing({
              playerId: playerId.value,
              gameTypeId: gameTypeId.value,
            })
          );
          gameTypeId.value = playerInfo.value.matches.gameTypeId;
          next.value = playerInfo.value.matches.next;
          matches.value = playerInfo.value.matches.rows;
        }
      } catch (error) {
        const alert = await alertController.create({
          header: " ",
          subHeader: `${error}`,
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } finally {
        loading.dismiss();

        const list = (searchWords.value as any[]).filter(data => data.word === playerName.value);
        list.forEach(async data => {
          await idb.delete(tableName, data.date);
        });
        await idb.put(tableName, { word: playerName.value, date: Date.now() }, Date.now());
        searchWords.value = await idb.getAll(tableName);
      }
    };

    const enter = (e: KeyboardEvent) => {
      if (e.key === "Enter") {
        setTimeout(() => {
          playerSearch();
        }, 250);
      }
    };

    const searchWordsClick = (word: string) => {
      playerName.value = word;
      playerSearch();
    };

    const searchWordsDelete = async (date: Date) => {
      await idb.delete(tableName, date);
      searchWords.value = await idb.getAll(tableName);
    };

    const getKDA = (data: { playInfo: { killCount: number, deathCount: number, assistCount: number } }) => {
      if (data.playInfo.deathCount > 0) {
        return Math.round(((data.playInfo.killCount + data.playInfo.assistCount) / data.playInfo.deathCount) * 100) / 100;
      } else {
        return "PERFECT";
      }
    };

    const addMatches = async (data: string) => {
      const loading = await loadingController.create({
        message: " ",
        mode: "ios",
      });
      loading.present();

      try {
        const json = await NeopleApi.cyPlayerMatches({
          playerId: playerId.value,
          next: data,
        });

        matches.value = [...matches.value, ...json.matches.rows];
        next.value = json.matches.next;
      } catch (error) {
        const alert = await alertController.create({
          header: " ",
          subHeader: `${error}`,
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } finally {
        loading.dismiss();
      }
    };

    const gameNormalRatio = computed(() => {
      let ratio = 0;
      let game = null;

      if (!Common.isNull(playerInfo)) {
        game = (playerInfo.value.records as any[]).filter(
          (game) => game.gameTypeId === "normal"
        )[0];
      }

      if (!Common.isNull(game)) {
        if (game.winCount + game.loseCount > 0) {
          ratio =
            Math.round(
              (game.winCount / (game.winCount + game.loseCount)) * 10000
            ) / 100;
        }
      }

      return ratio;
    });

    const gameNormalRatioColor = computed(() => {
      let color = "";

      if (gameNormalRatio.value >= 75) {
        color = "primary";
      } else if (gameNormalRatio.value >= 50) {
        color = "success";
      } else if (gameNormalRatio.value >= 25) {
        color = "warning";
      } else {
        color = "danger";
      }
      return color;
    });

    const gameRatingRatio = computed(() => {
      let ratio = 0;
      let game = null;

      if (!Common.isNull(playerInfo)) {
        game = (playerInfo.value.records as any[]).filter(
          (game) => game.gameTypeId === "rating"
        )[0];
      }

      if (!Common.isNull(game)) {
        if (game.winCount + game.loseCount) {
          ratio =
            Math.round(
              (game.winCount / (game.winCount + game.loseCount)) * 10000
            ) / 100;
        }
      }

      return ratio;
    });

    const gameRatingRatioColor = computed(() => {
      let color = "";

      if (gameRatingRatio.value >= 75) {
        color = "primary";
      } else if (gameRatingRatio.value >= 50) {
        color = "success";
      } else if (gameRatingRatio.value >= 25) {
        color = "warning";
      } else {
        color = "danger";
      }
      return color;
    });

    watch(gameTypeId, (newVal, oldVal) => {
      if (!Common.isNull(oldVal)) playerSearch();
    });

    watch(matches, () => {
      (matches.value as any[]).map(gameInfo => {
        gameInfo.playInfo.resultColor = gameInfo.playInfo.result === 'win' ? 'primary' : 'danger';
        gameInfo.playInfo.partyTypeName = gameInfo.playInfo.partyUserCount > 0 ? ` ${gameInfo.playInfo.partyUserCount}` : "";
        gameInfo.playInfo.kda = getKDA(gameInfo);

        return gameInfo;
      });
    });

    watch(searchWords, () => {
      (searchWords.value as any[]).sort((a, b) => b.date - a.date)
    })

    onIonViewDidLeave(() => {
      menuController.close("main-menu");
    });

    onMounted(async () => {
      idb = await openDB('jcu', 1, {
        upgrade(db) {
          if (!db.objectStoreNames.contains(tableName)) {
            db.createObjectStore(tableName);
          }
        },
      });

      searchWords.value = await idb.getAll(tableName);
    });

    return {
      searchSharp,
      close,
      NeopleApi,
      Common,
      playerName,
      playerInfo,
      gameNormalRatio,
      gameNormalRatioColor,
      gameRatingRatio,
      gameRatingRatioColor,
      gameTypeId,
      next,
      matches,
      searchWords,
      playerSearch,
      enter,
      addMatches,
      searchWordsClick,
      searchWordsDelete
    };
  },
});
