
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonSearchbar,
  IonButtons,
  IonBackButton,
  IonChip,
  loadingController,
  alertController,
} from "@ionic/vue";
import { computed, defineComponent, onMounted, ref, watch } from "vue";
import StoreCard from "@/components/StoreCard.vue";
import GoogleApi from "@/utils/GoogleApi";
import Common from "@/utils/Common";
import NoData from "@/components/NoData.vue";

export default defineComponent({
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonSearchbar,
    IonButtons,
    IonBackButton,
    StoreCard,
    IonChip,
    NoData,
  },
  setup() {
    const storeList = ref([] as any[]);
    const storeTypes = ["", "", ""];
    const storeTypeSearch = ref("");
    const sotreSearch = ref("");

    const searchWordChange = () => {
      storeList.value.map((store) => {
        const storeName = store["store-name"] as string;
        const storeType = store["store-type"] as string;

        switch (storeTypeSearch.value) {
          case "":
            store.show = storeName.includes(sotreSearch.value);
            break;

          default:
            store.show =
              storeName.includes(sotreSearch.value) &&
              storeType.includes(storeTypeSearch.value);
            break;
        }

        return store;
      });
    };

    const showStoreList = computed(() =>
      storeList.value.filter((store) => store.show)
    );

    watch(sotreSearch, searchWordChange);
    watch(storeTypeSearch, searchWordChange);

    onMounted(async () => {
      const loading = await loadingController.create({
        message: " ",
        mode: "ios",
      });
      loading.present();
      try {
        storeList.value = await GoogleApi.getSingleSheetData("");
        storeList.value.forEach((store) => (store.show = true));
      } catch (error) {
        const alert = await alertController.create({
          header: " ",
          subHeader: `${error}`,
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } finally {
        loading.dismiss();
      }
    });

    return {
      storeTypes,
      storeTypeSearch,
      sotreSearch,
      Common,
      showStoreList,
    };
  },
});
