
import { computed, defineComponent, onMounted, ref } from 'vue'
import {
    IonPage,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonButton,
    IonContent,
    IonList,
    IonItem,
    loadingController,
    alertController,
    IonLabel,
    IonThumbnail,
    IonTitle,
    IonBackButton
} from '@ionic/vue';
import NeopleApi from '@/utils/NeopleApi';
import { useRoute } from 'vue-router';

export default defineComponent({
    components: {
        IonPage,
        IonHeader,
        IonToolbar,
        IonButtons,
        IonButton,
        IonContent,
        IonList,
        IonItem,
        IonLabel,
        IonThumbnail,
        IonTitle,
        IonBackButton
    },
    setup() {
        const route = useRoute();
        const itemId = route.params.itemId as string;
        const itemInfo = ref();
        const detail = ref(false);

        const explain = computed(() => {
            if (detail.value) {
                return itemInfo.value.explainDetail;
            } else {
                return itemInfo.value.explain;
            }
        })

        const btnName = computed(() => detail.value ? '' : '');

        const detailView = () => {
            detail.value = !detail.value;
        }

        onMounted(async () => {
            const loading = await loadingController.create({
                message: " ",
                mode: "ios",
            });

            loading.present();

            try {
                itemInfo.value = await NeopleApi.cyItemsInfo({ itemId: itemId });
            } catch (error) {
                const alert = await alertController.create({
                    header: " ",
                    subHeader: `${error}`,
                    buttons: ["OK"],
                    mode: "ios",
                });

                await alert.present();
            } finally {
                loading.dismiss();
            }
        })

        return {
            itemInfo,
            NeopleApi,
            detailView,
            explain,
            btnName
        }
    },
})
